import React, { Component } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import {SEARCH_URL} from '../../config'
import './Form.css'
import Content from '../../Content/Content'

let timer;

class Form extends Component {

    state = {
        inputValue: "",
        page: 1,
        results: undefined
    }

    valueTarget = ({target}) => {
        this.setState({ inputValue: target.value })    
        clearTimeout(timer);
        timer = setTimeout(this.doSearch.bind(this, target.value), 500);
    }

    doSearch = async (val) => {
        const url = SEARCH_URL(val)

        const fetched = await fetch(url);
        const data = await fetched.json()

        console.log(data)

        this.setState({
            results: data.results
        })
    }
    

    render() {
        console.log(this.state.results)
        return <>
            <form className="form-inline order-0 center">
                <input className="form-control mr-sm-2" type="search" value={this.state.inputValue} placeholder="Search film" aria-label="Search" onChange={this.valueTarget} />
            </form>
            {   this.state.results
                ?                 
                <div className="container-fluid">
                    <div className="card-deck row justify-content-center my-5">
                        {   
                            this.state.results.length > 0
                            ?
                            <Content
                                results={this.state.results}
                                path="/"
                            />
                            :
                            "not found"
                        }

                    </div>
                </div>
                : ""
            }
        
        </>
    }
}

export default Form
