import React, { Component } from 'react'
import Content from '..//Content/Content'
import './Popular.css'
import {POPULAR_URL} from '../config'

class Popular extends Component {
    state = {
        results: [],
        page: 1
    }

    async componentDidMount() {
        const fetched = await fetch(POPULAR_URL());
        const data = await fetched.json()
        this.setState({
            results: data.results,
            page: this.state.page + 1
        })
    }

    handlerLoading = async () => {
        const fetched = await fetch(POPULAR_URL(this.state.page + 1));
        const data = await fetched.json()
        this.setState((state) => {
            return {
                results: [...state.results, ...data.results],
                page: state.page + 1
            }
        }, console.log(this.state))
    }
     
    render() {
        console.log("render")
        return (
            <>
                <h1 className="display-1 mx-auto">Popular</h1>
                <div className="container-fluid">
                    <div className="card-deck row justify-content-center my-5">
                        <Content 
                            results={this.state.results}
                            path="/"
                        />
                    </div>
                </div>
                <button 
                className="btn btn-info mx-auto center"
                onClick={this.handlerLoading}>Load more..</button>  
                
            </>
        )
    }
}


export default Popular;